<template>
  <v-container>
    <div v-if="currentIns" class="edit-form py-3">
      <v-row>
        <v-col class="text-center" cols="12" md="4">
          <v-row class="pa-5">
            <h2>EDIT INSURER</h2>
          </v-row>
        </v-col>

        <v-col cols="12" md="4">
          <v-spacer />
          <v-btn
            to="/addIns"
            class="text-decoration-none"
            color="primary"
            dark
            block
            small
            >ADD INSURER</v-btn
          >
        </v-col>

        <v-col cols="12" md="4">
          <v-spacer />
          <v-btn
            to="/inss"
            class="text-decoration-none"
            color="primary"
            dark
            block
            small
            >INSURER LIST</v-btn
          >
        </v-col>
      </v-row>

      <v-form ref="form" lazy-validation>
        <v-text-field
          v-model="currentIns.name"
          :rules="[(v) => !!v || 'Name is required']"
          label="Name"
          required
        ></v-text-field>

        <v-text-field
          v-model="currentIns.insCode"
          :rules="[(v) => !!v || 'Code is required']"
          label="Code"
          required
        ></v-text-field>

        <v-btn color="error" small class="mr-2" @click="deleteIns">
          Delete
        </v-btn>

        <v-btn color="success" small @click="updateIns"> Update </v-btn>
      </v-form>

      <p class="mt-3">{{ message }}</p>
    </div>

    <div v-else>
      <p>Please click on a User...</p>
    </div>
  </v-container>
</template>

<script>
import InsDataService from "../../services/InsDataService";

export default {
  name: "insurer",
  data() {
    return {
      currentIns: null,
      message: "",
    };
  },
  methods: {
    getIns(id) {
      InsDataService.get(id)
        .then((response) => {
          this.currentIns = response.data;
          // console.log(response.data);
        })
        .catch((e) => {
          console.log(e);
        });
    },

    updateIns() {
      InsDataService.update(this.currentIns.id, this.currentIns)
        .then((response) => {
          // console.log(response.data);
          this.$store.dispatch("setSnackbar", {
            showing: true,
            text:
              (response && response.data && response.data.message) ||
              toString(),
          });
          this.$router.push({ name: "inss" });
          // this.message = "The Insurer was updated successfully!";
        })
        .catch((e) => {
          console.log(e);
        });
    },

    deleteIns() {
      InsDataService.delete(this.currentIns.id)
        .then((response) => {
          // console.log(response.data);
          this.$store.dispatch("setSnackbar", {
            showing: true,
            text:
              (response && response.data && response.data.message) ||
              toString(),
          });
          this.$store.dispatch("setSnackbar", {
            showing: true,
            text:
              (response && response.data && response.data.message) ||
              toString(),
          });
          this.$router.push({ name: "inss" });
        })
        .catch((e) => {
          console.log(e);
        });
    },
  },
  mounted() {
    this.message = "";
    this.getIns(this.$route.params.id);
  },
};
</script>
